	margin: auto;
	background-color: #523f39;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
#guessingCompetition .camp{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	height: 100%;
}
#guessingCompetition .left{
	background-color: #8b332f;
}
#guessingCompetition .right{
	background-color: #587593;
}
.gCCamp{
	margin-top: 1.5vw !important;
}

.gCCamp{
	width: 40.5vw;
	height: 13.5vw;
	margin: auto;
	outline: #cfa5b1 2px solid;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.gCShikigami{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	height: 100%;
	border: 1px solid #000000;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
}

#GCCampAttribute{
	position: fixed;
	width: 94vw;
	height: 47vw;
	background-color: #8b6851;
	left: 50%;
	margin-left: -47vw;
	top:50%;
	margin-top: -23.5vw;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
#GCCampAttribute .closeCampAttributeButton{
	width: 5.897vw;
	height: 5.897vw;
	line-height: 5.897vw;
	text-align: center;
	font-size: 3vw;
	border-radius: 50%;
	background: #783a45;
	right: -2vw;
	top:1vw;
	position: absolute;
}
#GCCampAttribute .table{
	background-color: #c7b59e;
	margin: auto;
	width: 97%;
	height: 94%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
#GCCampAttribute .table .tr{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	    -ms-flex-direction:column;
	        flex-direction:column;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
#GCCampAttribute .table .tr:first-of-type{
	-webkit-box-flex: initial;
	    -ms-flex: initial;
	        flex: initial;
	width:12.8%;
}
#GCCampAttribute .table .tr .td{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-line-pack:center;
	    align-content:center;
	-webkit-box-pack:center;
	    -ms-flex-pack:center;
	        justify-content:center;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	font-size: 1.5vw;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	    -ms-flex-direction:column;
	        flex-direction:column;
}
#GCCampAttribute .table .tr .td img{
	background-color: #CFA5B1;
	width: 5.5vw;
	height: 5.5vw;
}
#GCCampAttribute .table .tr .td:first-of-type{
	height: 19.7%;
	-webkit-box-flex: initial;
	    -ms-flex: initial;
	        flex: initial;
	font-size: 1.7vw;
}
#GCCampAttribute .table .tr .td:last-of-type{
	height: 13.3%;
	-webkit-box-flex: initial;
	    -ms-flex: initial;
	        flex: initial;
}
